<template>
    <div class="couponcelll">
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh" class="refresh">
            <van-nav-bar
            title="我的订单"
            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
            />
            <div v-for="(item, index) in data" :key="index" style="margin-top:4px;">
                <van-card
                    :num="item.num"
                    :tag="item.tag"
                    :price="item.price"
                    :desc="item.desc"  
                    :title="item.title"
                    :thumb="item.imageURL"
                    :origin-price="item.originprice"
                >
                <div slot="footer">
                    <van-button size="mini" @click="del(index)">删除订单</van-button>
                </div>
                </van-card>
            </div>
        <div style="height:65px;">&nbsp;</div>
        </van-pull-refresh>
        <foot-nav></foot-nav>
    </div>
</template>
<script>
import footNav from "@@/footnav.vue"

export default {
    components: {
        footNav
    },
    created() {
        document.title = '我的';
    },

    data(){
        return {
            value : "",
            isLoading: false,
            data : [
                {
                    imageURL : require("../assets/img/40.png"),
                    tag: '已支付',
                    price: '20.0',
                    originprice: '22.8',
                    title: '草莓冰果',
                    desc: '少冰 半糖 中杯',
                    num: 2
                },
                {
                    imageURL : require("../assets/img/12.png"),
                    tag: '已支付',
                    price: '29.0',
                    originprice: '32.8',
                    title: '彩虹冰淇淋',
                    desc: '8分糖',
                    num: 1
                },
                {
                    imageURL : require("../assets/img/0321135651.png"),
                    tag: '已取消',
                    price: '0.0',
                    originprice: '12.8',
                    title: '草莓慕斯',
                    desc: '7.8寸',
                    num: 1
                },
                {
                    imageURL : require("../assets/img/551.png"),
                    tag: '未支付',
                    price: '16.8',
                    originprice: '16.8',
                    title: '红柚气泡水',
                    desc: '正常',
                    num: 3
                },
                {
                    imageURL : require("../assets/img/WechatIMG9.jpeg"),
                    tag: '已支付',
                    price: '38',
                    originprice: '58',
                    title: '水果披萨',
                    desc: '13寸 5种水果',
                    num:1
                }
            ]
        }
    },
    methods: {
        onRefresh() {
            setTimeout(() => {
                this.$toast('刷新成功');
                this.isLoading = false;
                this.count++;
            }, 500);
        },
        onClickLeft () {
            this.$router.push("/my")
        },
        del (index) {
            this.$toast('删除成功');
            this.data.splice(index, 1);
        }
    }
}
</script>

<style scoped>
.couponcelll{
    text-align: left;
    min-height: 80vh;
}
</style>
