            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
            />
            <van-address-list
                v-model="chosenAddressId"
                :list="list"
                :disabled-list="disabledList"
                disabled-text="以下地址超出配送范围"
                @add="onAdd"
                @edit="onEdit"
            />
        <div style="height:50px;">&nbsp;</div>
        </van-pull-refresh>
        <foot-nav></foot-nav>
    </div>
</template>
<script>
import footNav from "@@/footnav.vue"

export default {
    components: {
        footNav
    },
    created() {
        document.title = '我的';
    },

    data(){
        return {
            value : "",
            isLoading: false,
            chosenAddressId: '1',
            list: [
                {
                id: '1',
                name: 'PIEXIE',
                tel: '13960342290',
                address: '天津市南开区读灵小区27幢1单元801'
                },
                {
                id: '2',
                name: '张小姐',
                tel: '13960342290',
                address: '天津市南开区明豪大厦8F'
                }
            ],
            disabledList: [
                {
                id: '3',
                name: '张小姐',
                tel: '13960342290',
                address: '浙江省杭州市滨江区江南大道15号'
                }
            ]
        }
    },
    methods: {
        onRefresh() {
            setTimeout(() => {
                this.$toast('刷新成功');
                this.isLoading = false;
                this.count++;
            }, 500);
        },
        onClickLeft () {
            this.$router.push("/my")
        },
        onAdd() {
            this.$router.push("/editAd")
        },
        onEdit() {
            this.$router.push("/editAd")
        }
    }
}
</script>

<style scoped>
.couponcelll{
    text-align: left;
    min-height: 80vh;
}
</style>
