<template>
    <div class="couponcelll">
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh" class="refresh">
            <van-nav-bar
            title="我的地址"
            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
            />
            <van-address-edit
                :area-list="areaList"
                show-postal
                show-delete
                show-set-default
                show-search-result
                :search-result="searchResult"
                @save="onSave"
                @delete="onDelete"
                @change-detail="onChangeDetail"
            />
        <div style="height:50px;">&nbsp;</div>
        </van-pull-refresh>
        <foot-nav></foot-nav>
    </div>
</template>
<script>
import footNav from "@@/footnav.vue"
import  areaList  from "@/assets/area/area";

export default {
    components: {
        footNav
    },
    created() {
        document.title = '我的';
    },

    data(){
        return {
            value : "",
            isLoading: false,
            chosenAddressId: '1',
            areaList,
            searchResult: []
        }
    },
    methods: {
        onRefresh() {
            setTimeout(() => {
                this.$toast('刷新成功');
                this.isLoading = false;
                this.count++;
            }, 500);
        },
        onClickLeft () {
            this.$router.push("/my")
        },
        onSave() {
             this.$toast('保存成功');
        },
        onDelete() {
            this.$toast('删除成功');
            this.$router.push("/my")
        },
        onChangeDetail(val) {
            if (val) {
                this.searchResult = [{
                name: '南开大悦城',
                address: '天津市南开区'
                }];
            } else {
                this.searchResult = [];
            }
        }
    }
}
</script>

<style scoped>
.couponcelll{
    text-align: left;
    min-height: 80vh;
}
</style>
<style>
</style>



