<template>
    <div class="couponcelll">
        <!-- <van-pull-refresh v-model="isLoading" @refresh="onRefresh" class="refresh"> -->
            <van-nav-bar
            title="向商家付钱"
            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
            />
            <van-coupon-cell
                :coupons="coupons"
                :chosen-coupon="chosenCoupon"
                @click="showList = true"
            />
            <div style="text-align:center;margin-top:20px">
                <img src="~@/assets/img/1553134349.png" alt="">
            </div>
            <van-popup v-model="showList" position="bottom">
                <van-coupon-list
                    :coupons="coupons"
                    :chosen-coupon="chosenCoupon"
                    :disabled-coupons="disabledCoupons"
                    @change="onChange"
                    @exchange="onExchange"
                />
            </van-popup>
        <!-- <div style="height:100vh;">&nbsp;</div> -->
        <!-- </van-pull-refresh> -->
        <foot-nav></foot-nav>
    </div>
</template>
<script>
import footNav from "@@/footnav.vue"
const coupon = {
  available: 1,
  condition: '无使用门槛\n最多优惠12元',
  reason: '',
  value: 550,
  name: '优惠券名称',
  startAt: 1559104000,
  endAt: 1585592000,
  valueDesc: '5.5',
  unitDesc: '元'
};

export default {
    components: {
        footNav
    },
    created() {
        document.title = '我的';
    },

    data(){
        return {
            value : "",
            isLoading: false,
            chosenCoupon: -1,
            coupons: [coupon],
            disabledCoupons: [coupon],
            showList : false
        }
    },
    methods: {
        onRefresh() {
            setTimeout(() => {
                this.$toast('刷新成功');
                this.isLoading = false;
                this.count++;
            }, 500);
        },
        onClickLeft () {
            this.$router.push("/my")
        },
        onChange(index) {
            this.showList = false;
            this.chosenCoupon = index;
        },
        onExchange() {
            this.coupons.push(coupon);
        }
    }
}
</script>

<style scoped>
.couponcelll{
    text-align: left;
    min-height: 80vh;
}
</style>
